.layout-container {
  display: flex;
  flex-direction: column; /* Changed to column */
  min-height: 100vh;
  position: relative;
  overflow: hidden; /* Prevent potential overflow issues */
}

/* MAIN CONTENT */
.main-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  transition: margin-left 0.3s ease;
  /* Remove margin-top, let the padding handle the spacing */
}

.header-bar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  background-color: #f8f8f8;
  height: 50px;
  padding: 0 15px;
  border-bottom: 1px solid #ddd;
  z-index: 100;
}

.page-content {
  flex: 1;
  padding: 0px 0px 60px 0px;/* Adjusted padding: top, right, bottom, left */
  overflow-y: auto;
  background-color: #fff;
  /* min-height: calc(100vh - 50px - 60px); /* top header + bottom nav */
  position: relative; /* Ensure content is positioned relative to avoid overlap */
  z-index: 1; /* Ensure content is above potential overlapping elements */
}

.welcome-message {
  position: relative;
  z-index: 0;       /* ensures it stays behind the header-bar (z-index: 100) */
  background: none; /* removes background */
}

.home-header {
  background: none;
}
