.home-container {
  padding: 20px;
  /* RECENT TRANSACTIONS */
  .recent-transactions {
    background: #fff;
    padding: 15px;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0,0,0,0.1);
  }
  
  .recent-transactions h3 {
    margin-top: 0;
  }
  
  .recent-transactions ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .recent-transactions li {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 10px;
    border-bottom: 1px solid #eee;
    padding-bottom: 5px;
  }
  
  .tx-icon {
    font-size: 20px;
    margin-right: 10px;
  }
  
  .tx-details {
    flex: 1;
  }
  
  .tx-time {
    font-size: 12px;
    color: #999;
    display: block;
  }
  
  .tx-amount {
    font-weight: 600;
    margin-left: 10px;
  }
}

/* Header Section */
.home-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  background-color: #f0f0f0; /* Or any desired background color */
  padding: 10px;
  text-align: center;
  position: sticky; /* This makes the header stick to the top */
  top: 0; /* Stick to the very top of the viewport */
  z-index: 100; /* Ensure it's above other content */
}

/* Month Selection Tabs */
.month-tabs {
  display: flex;
  overflow-x: auto;
  margin-bottom: 20px;
}

.month-tabs button {
  background: none;
  border: none;
  padding: 10px 15px;
  margin-right: 5px;
  cursor: pointer;
  border-radius: 5px;
}

.month-tabs button.active {
  font-weight: bold;
  /* Add more styling for active tab */
}

/* Financial Overview Section */
.financial-overview {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  margin-bottom: 20px;
}

.financial-overview > div {
  background-color: #fff;
  padding: 15px;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.balance-summary .owe {
  color: red;
}

.balance-summary .owed {
  color: green;
}

/* Recent Transactions List */
.recent-transactions {
  background: #fff;
  padding: 15px;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.recent-transactions h3 {
  margin-top: 0;
}

.recent-transactions ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.recent-transactions li {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
  border-bottom: 1px solid #eee;
  padding-bottom: 5px;
}

.tx-desc {
  flex: 1;
}

.tx-category {
  font-size: 12px;
  color: #999;
  display: block;
}

.tx-time {
  font-size: 12px;
  color: #999;
  display: block;
}

.tx-amount {
  font-weight: 600;
  margin-left: 10px;
}

/* DEBUG SECTION */
.debug-section {
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  padding: 15px;
  margin-top: 20px;
  border-radius: 8px;
  overflow-x: auto; /* Enable horizontal scrolling for long content */
}

.debug-section h3 {
  margin-top: 0;
  margin-bottom: 10px;
}

.debug-section pre {
  font-size: 12px;
  color: #333;
  white-space: pre-wrap; /* Allows the content to wrap */
}

/* Raw DB Display */
.raw-db-display {
  margin-top: 20px;
  padding: 10px;
  border: 1px solid #ccc;
  background-color: #f9f9f9;
  overflow-x: auto;
}

.raw-db-display h3 {
  font-size: 16px;
  margin-bottom: 10px;
}

.raw-db-display pre {
  font-size: 12px;
  font-family: monospace;
  white-space: pre-wrap;
  word-wrap: break-word;
}
