.bottom-nav {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 60px;
  background-color: #f8f8f8;
  border-top: 1px solid #ddd;
  display: flex;
  justify-content: space-around;
  align-items: center;
  z-index: 1000;
}

.nav-item {
  text-decoration: none;
  color: #555;
  font-size: 24px; /* Increase font size for icons */
  padding: 8px;
}

.nav-item:hover,
.nav-item.active {
  color: #007bff;
  font-weight: 500;
}
