/* Global CSS styles and resets for the application */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Styling for action buttons including hover effects */
.action-btn {
  transition: background-color 0.2s ease;
}

.action-btn:hover {
  background-color: #0056b3; /* Darker shade of brand color on hover */
}

/* Code element styling for inline code blocks */
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
