.quick-actions-fab {
    position: fixed;
    bottom: 80px; /* adjust to sit above your bottom nav */
    right: 20px;
    display: flex;
    flex-direction: column;
    gap: 16px;
    z-index: 1000;
  }
  
  .fab {
    width: 56px;
    height: 56px;
    border-radius: 50%;
    background-color: #007bff;
    color: white;
    font-size: 24px;
    border: none;
    outline: none;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
    cursor: pointer;
    transition: background-color 0.3s ease, box-shadow 0.3s ease;
  }
  
  .fab:hover {
    background-color: #0056b3;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  }

.quick-actions {
  position: fixed;
  right: 0;
  bottom: 0;
  pointer-events: none; /* Ensure clicks pass through container */
}

/* Override pointer events for fabs */
.quick-actions .fab {
  pointer-events: auto;
}
